/*
NodeJS 中 导出 模块
导出模块 module.exports = {} 或 exports.属性名 = 值

这是 NodeJS 导出数据的方式

*/ 

/*
Vue 中导出 组件的 方式

第一种方式 完整导出 和 导入
    完整导出 export default { ... }
    完整导入 import 组件名或数据名称 from '来自哪一个路径'
             import xxx from './'

第二种方式 按需导出 和 导入
    按需导出：    export const a = 10;
                  export const b = 30;
                  export const b = 35;

    按需导入：
    import {a, b} from './api/index.js'
    console.log(a, b);

    按需导出的 名字 与 按需导入的名字需要 一一对应
*/ 

// 封装 请求
import axios from 'axios'
// 需要 引入组件 Toast 
import { Toast } from 'vant'


// 全局的 axios 默认值
axios.defaults.baseURL = 'http://www.lovegf.cn:9527/api';

/* --------------------------------------------首页-------------------------------------*/ 
// 获取 轮播数据
export const catchSliders =  () => {
    return axios.get("/home/sliders").then(res => {
                // 判断数据 是否请求成功
                if (res.data.status == 0) {
                    return res.data.data;
                } else {
                    // console.log("获取 轮播数据失败");
                    Toast("接口错误");
                }
            }).catch(err => {
                // console.log(err);
                Toast("接口错误");
            });
}


// 获取首页导航分类
export const catchCategory = () => {
    return  axios.get('/home/category').then(res => {
        if (res.data.status == 0) {
            return res.data.data
        } else {
            Toast('接口异常')
        }
    }).catch(err => {
        Toast('接口异常' + err.message)
    })
}


// 获取 首页楼层商品展示数据
export const catchFloorList = () => {
    return  axios.get('/home/floorlist').then(res => {
                if(res.data.status == 0) {
                    return res.data.data
                } else {
                    // 使用 接口返回的提示信息 
                    Toast(res.data.msg)
                }
            }).catch(err => {
                Toast('接口异常'+ err.message)
            })
}

/* --------------------------------------------商品-------------------------------------*/ 

// 获取 商品列表数据
export const catchProductList = (params) => {

    // 参数的设置方式 这样写存在问题
    // return axios.get('/product/list', params).then(res => {
    //                     if(res.data.status == 0) {
    //                         return res.data.data
    //                     } else {
    //                         // 使用 接口返回的提示信息 
    //                         Toast(res.data.msg)
    //                     }
    //                 }).catch(err => {
    //                     Toast('接口异常'+ err.message)
    //                 })

    // 发送 带参数  以 query 方式提交 可以 这样传参
    return axios.get('/product/list', {
        'params': params
    }).then(res => {
            if(res.data.status == 0) {
                return res.data.data
            } else {
                // 使用 接口返回的提示信息 
                Toast(res.data.msg)
            }
        }).catch(err => {
            Toast('接口异常'+ err.message)
        })
}

// 获取商品详情 
export const catchProductDetail = params => {
    return axios.get('/product/detail', {
            'params': params
        }).then(res => {
                if(res.data.status == 0) {
                    return res.data.data
                } else {
                    // 使用 接口返回的提示信息 
                    Toast(res.data.msg)
                }
            }).catch(err => {
                Toast('接口异常'+ err.message)
            })
}