<template>
  <div id="app">
    <transition :name="flage ? 'ani' : ''">
      <router-view />
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flage: false
    }
  },
  updated() {
    // console.log(this.$route.matched);
  },
  watch: {
    '$route': function (to, from) {
      // console.log('to:', to);
      // console.log('from:', from);
      /*
      1 级页面 到 2级 页面 动画是 从 右到左
      2 级页面 返回 1级页面 动画 从 左到右
      */  
    //  console.log(to.meta.index);
    //  console.log(from.meta.index);
     
     if(from.meta.index > to.meta.index) {
       this.flage = true
     } else {
       this.flage = false
     }
     
    }
  }
}
</script>

<style lang="scss">

.v-enter {
  transform: translateX(100%)
}

.v-enter-active,
.ani-enter-active {
  transition: all 0.5s ease;
}
.v-enter-to {
  transform: translateX(0)
}

.ani-enter {
  transform: translateX(0)
}
.ani-enter-to {
  transform: translateX(100%)
}



</style>
